<script>
import Breadcrumbs from "../../../components/breadcrumbs/breadcrumbs.vue";
import Module1 from "./views/module1.vue";
import { setTimeout } from "timers";
export default {
  components: {
    Breadcrumbs,
    Module1
  },
  data() {
    return {
      user_id: "",
      allList: {
        disease:[],
        trauma:[],
        medicine:[]
      },
      img:"",
      items: [
        {
          text: "用户列表",
          disabled: false,
          to: "/userList"
        },
        {
          text: "用户病例档案",
          disabled: false,
          to: "/files"
        }
      ]
    };
  },
  mounted: function() {
    this.info();
  },
  methods: {
    loader_() {
      this.loader = true;
      setTimeout(() => (this.loader = false), 500);
    },
    // 时间颗粒度
    rlist(data) {
      let datas = {
        state: true,
        list: data
      };
      this.$refs.module1.dialogShow(datas);
    },
    info() {
      this.loader = true;
      this.user_id = this.$route.query.user_id - 0;
      this.$axios({
        method: "post",
        url: this.$apis.URL.manages_cases_file,
        data: {
          user_id: this.user_id
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            this.allList = response.data.datas;
            this.img = this.$route.query.img;
          } else if (response.data.result === 1003) {
            this.$Dialog.getProductCheckResult_({
                text: `${response.data.message} 该用户暂无病例档案数据!`,
                timeout: 3000,
                snackbar: true
            })
            setTimeout(() => {
              window.history.back(-1);
            }, 1500);
          } else {
            this.$Dialog.getProductCheckResult_({
                text: response.data.message,
                timeout: 3000,
                snackbar: true
            })
          }
          this.loader = false;
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({
                text: error,
                timeout: 3000,
                snackbar: true
            })
        });
    }
  },
  watch: {}
};
</script>

<style lang="less">
#files_ {
  .v-list--two-line .v-list__tile {
    min-height: 72px;
    height: auto !important;
  }
